.portfolioContainer {
	position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 0px;
  height: 100% !important;;
}
.portfolioTitle {
  color: #fff;
  width: 100%;
  text-align: center;
  letter-spacing: 4px;
  font-weight: 500;
  padding: 20px 0;
  margin-bottom: 1em;
	z-index: 3;
}
.portfolioTitle h1 {
  font-family: "Josefin Sans", sans-serif;
	font-size: 50px;
}
.portfolioGrid {
  display: grid;
  width: 80%;
  max-width: 2000px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  grid-gap: 3em;
	z-index: 3;
	border-radius: 15px;
	padding: 30px;
}
.portfolio-fade-in {
	opacity: 1;
}
.buttonsContainer {
  max-width: 1200px;
  width: 100%;
  display: flex;
