}

.loadingScreen {
  width: 100vw;
  height: 100vh;
  top: 0;
  position: fixed;
  z-index: 9999;
  display: none;
}
.loadingScreenText {
  font-size: 2em;
  font-weight: 300;
  color: #eee;
  position: absolute;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  opacity: 0;
  z-index: 9999;
}
.loadingBottomSlider {
  background: #202020;
  height: 50%;
  width: 100vw;
  bottom: 0;
  transform: translateX(110%);
  position: absolute;
  z-index: 9998;
}
.loadingTopSlider {
  background: #252525;
  height: 50%;
  width: 100vw;
  top: 0;
  transform: translateX(-110%);
  position: absolute;
  z-index: 9998;
}

.loadingScreenVisible {
  display: block;
}
.loadingSlidersSlideInLeft {
  -webkit-animation: slidersSlideInLeft 2500ms;
          animation: slidersSlideInLeft 2500ms;
}
@-webkit-keyframes slidersSlideInLeft {
  0% {
    transform: translateY(-110%);
  }
  20% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateX(-110%);
  }
}
@keyframes slidersSlideInLeft {
  0% {
    transform: translateY(-110%);
  }
  20% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateX(-110%);
  }
}
.loadingSlidersSlideInRight {
  -webkit-animation: slidersSlideInRight 2500ms;
          animation: slidersSlideInRight 2500ms;
}
@-webkit-keyframes slidersSlideInRight {
  0% {
    transform: translateY(110%);
  }
  20% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateX(110%);
  }
}
@keyframes slidersSlideInRight {
  0% {
    transform: translateY(110%);
  }
  20% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateX(110%);
  }
}
.loadingTextFadeIn {
  opacity: 0;
  -webkit-animation: textFadeIn 1250ms;
          animation: textFadeIn 1250ms;
}
@-webkit-keyframes textFadeIn {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes textFadeIn {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@media screen and (max-width: 800px) {
  .portfolioGrid {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(1, minmax(100px, 1fr));
    grid-column-gap: 5em;
  }
  .portfolioContainer {
    padding: 100px 0px;
  }
  .portfolioGrid {
    padding: 0px;
  }
}

@media screen and (max-width: 600px) {
